.App {
  height: 100vh;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  width: 100%;
}

.ControlPanel,
.MapContainer {
  height: 100vh;
  display: block;
  vertical-align: top;
}

.ControlPanel {
  width: 20rem;
  -ms-flex-order: 1;
      order: 1;
}

.MapContainer {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-order: 2;
      order: 2;
}

@media screen and (max-width: 720px) {
  .App {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .ControlPanel {
    width: 100%;
    -ms-flex-order: 2;
        order: 2;
    height: 40vh;
  }

  .MapContainer {
    width: 100%;
    -ms-flex-order: 1;
        order: 1;
    height: 60vh;
  }
}



